import styled from 'styled-components';

const bg = '#333333';

export const Card = styled.div`
    position: relative;
    min-height: 300px;
    background-color: ${bg};
    text-align: center;
    padding: 2em;
    border-radius: 5px;
`;


export const CardImg = styled.img`
    position: absolute;
    object-fit: cover;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 5px;
`;


export const CardLayout = styled.div`
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content:center;
    align-items:center;

    & div {
            & h3, p {
            color: transparent;
        }

        & h3 {
            font-size: 1.4rem !important;
            text-transform: uppercase;
        }
    }

    &:hover {
        background: rgba(0,0,0,80%);

        & div {
            & h3, p {
                color: #FFFFFF;
            }

            & h3 {
                font-size: 1.4rem !important;
                text-transform: uppercase;
            }
        }
    }
    
`;

// media queries 
// flex-direction: column;
export const Menu = styled.ul`
    list-style-type: none;
    display: flex;
    flex-direction: row;
    justify-content: center;

    & li {
        margin-right: 1em;

        & a {
            font-size: .8rem;
            text-decoration: None;
            color: #FFFFFF;
        }
    }
`;